html {
    scroll-behavior: smooth;
}
.about {
    background-color: #5e63b6;
    min-height: 100vh;
    padding: 2rem; 
    margin-top: -40px;
}

.aboutcon {
    font-size: 2rem;
    margin-bottom: 2rem;
    text-align: center;
}

.content-container {
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.content-item {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 2rem;
}

.abocon1, .abocon2, .abocon3, .abocon4 {
    flex: 1;
    margin: 1rem;
    margin-top: 50px;
    font-size: 1.5rem;
}

.abopic1, .abopic2, .abopic3, .abopic4 {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.abopic1 img, .abopic2 img, .abopic3 img, .abopic4 img {
    height: 300px; 
    width: auto; 
    max-width: 100%;
    border-radius: 10px;
}


img {
    max-width: 100%;
    height: auto;
}